/*
  Stranica prikazuje detalje serije, popis glumaca i popis epizoda.
  Omogućuje dodavanje u favorite i navigaciju na detalje epizode.
*/
import { notFound } from "next/navigation";
import Link from "next/link";
import FavoriteButton from "@/components/FavoriteButton";

// Tip za podatke o seriji (TVmaze API)
type Show = {
  name: string;
  summary: string;
  image?: {
    original: string;
  };
  genres: string[];
  rating: { average: number };
  status: string;
};

export default async function ShowDetail({ params }: { params: { id: string } }) {
  // Dohvaćamo podatke o seriji, glumcima i epizodama s TVmaze API-ja
  const { id } = params; // params is already available as a prop, no need to await
  const res = await fetch(`https://api.tvmaze.com/shows/${id}`);
  if (!res.ok) return notFound();
  const show: Show = await res.json();

  // Cast i episodes dohvaćamo odvojeno jer su to posebni endpointi
  const castRes = await fetch(`https://api.tvmaze.com/shows/${id}/cast`);
  const cast = castRes.ok ? await castRes.json() : [];
  const episodesRes = await fetch(`https://api.tvmaze.com/shows/${id}/episodes`);
  const episodes = episodesRes.ok ? await episodesRes.json() : [];

  // Dohvati crew podatke za seriju
  const crewRes = await fetch(`https://api.tvmaze.com/shows/${id}/crew`);
  const crew = crewRes.ok ? await crewRes.json() : [];

  // Zašto: Korištenje id iz params i fetch bez cache-a osigurava svjež prikaz podataka (vidi Next.js docs)

  return (
    <div className="p-4 bg-gradient-to-br from-blue-50 to-green-50 min-h-screen">
      <div className="flex flex-col md:flex-row gap-8">
        {/* Poster lijevo */}
        <div className="flex-shrink-0">
          <img src={show.image?.original} alt={show.name} className="w-full max-w-xs rounded shadow-lg border-4 border-green-200" />
        </div>
        {/* Detalji desno */}
        <div className="flex-1">
          <h1 className="text-4xl font-black mb-2 text-blue-900 drop-shadow">{show.name}</h1>
          <div className="mb-4 text-gray-800" dangerouslySetInnerHTML={{ __html: show.summary }} />
          <div className="mb-2">
            <span className="bg-green-200 text-green-900 px-2 py-1 rounded mr-2 font-semibold">Žanrovi:</span>
            {show.genres.join(", ")}
          </div>
          <div className="mb-2">
            <span className="bg-blue-200 text-blue-900 px-2 py-1 rounded mr-2 font-semibold">Status:</span>
            {show.status}
          </div>
          <div className="mb-4">
            <span className="bg-yellow-200 text-yellow-900 px-2 py-1 rounded mr-2 font-semibold">Ocjena:</span>
            {show.rating.average ?? "N/A"}
          </div>
          <FavoriteButton showId={parseInt(id)} />
        </div>
      </div>
      {/* Cast i crew jedan ispod drugog, ispod detalja */}
      <div className="mt-10">
        {/* Cast sekcija */}
        <h2 className="text-2xl font-bold mb-2 text-green-800">Glumačka postava</h2>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-8">
          {cast.slice(0, 8).map((c: any, idx: number) => (
            <div key={`${c.person.id}-${encodeURIComponent(c.character.name)}-${idx}`} className="rounded shadow p-2 flex flex-col items-center bg-white border border-green-100 hover:shadow-lg transition">
              <Link href={`/actors/${c.person.id}`}>
                <img
                  src={c.person.image?.medium || c.character.image?.medium || "/file.svg"}
                  alt={c.person.name}
                  className="w-20 h-28 object-cover rounded mb-2 border border-blue-200 cursor-pointer hover:opacity-80 transition"
                  width={80}
                  height={112}
                />
              </Link>
              <div className="text-center">
                <div className="font-semibold text-blue-900">{c.person.name}</div>
                <div className="text-sm text-green-700">kao {c.character.name}</div>
              </div>
            </div>
          ))}
        </div>
        {/* Crew sekcija ispod casta */}
        {crew.length > 0 && crew.some((c: any) => c.person && c.type) && (
          <div className="mt-10">
            <h2 className="text-2xl font-bold mb-2 text-purple-800">Ekipa (crew)</h2>
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-8">
              {crew.slice(0, 12).map((c: any, idx: number) => (
                c.person && c.type && (
                  <div key={`${c.person.id}-${c.type}-${idx}`} className="rounded shadow p-2 flex flex-col items-center bg-white border border-purple-100 hover:shadow-lg transition">
                    <Link href={`/actors/${c.person.id}`}>
                      <img
                        src={c.person.image?.medium || "/file.svg"}
                        alt={c.person.name}
                        className="w-20 h-28 object-cover rounded mb-2 border border-purple-200 cursor-pointer hover:opacity-80 transition"
                        width={80}
                        height={112}
                      />
                    </Link>
                    <div className="text-center">
                      <div className="font-semibold text-purple-900">{c.person.name}</div>
                      <div className="text-sm text-purple-700">{c.type}</div>
                    </div>
                  </div>
                )
              ))}
            </div>
          </div>
        )}
      </div>
      {/* Detalji ispod */}
      <div className="mt-10">
        <h2 className="text-2xl font-bold mb-2 text-blue-800">Epizode</h2>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          {episodes.slice(0, 4).map((episode: any) => (
            <div key={id + '-' + episode.id} className="border p-4 rounded bg-white shadow hover:shadow-lg transition">
              <Link href={`/shows/${id}/episodes/${episode.id}`} className="text-blue-700 hover:text-green-700 font-semibold text-lg underline">
                Sezona {episode.season}, Epizoda {episode.number}: {episode.name}
              </Link>
              <p className="text-sm text-gray-600">Datum emitiranja: {episode.airdate}</p>
              {episode.image && (
                <img src={episode.image.medium} alt={episode.name} className="mt-2 rounded border border-blue-100" />
              )}
            </div>
          ))}
        </div>
        {episodes.length > 4 && (
          <div className="mt-4 text-center">
            <Link href={`/shows/${id}/episodes`} className="inline-block bg-blue-100 hover:bg-blue-200 text-blue-800 font-semibold px-4 py-2 rounded transition">
              Prikaži sve epizode
            </Link>
          </div>
        )}
      </div>
    </div>
  );
}
